import React from "react";
import AppsContainer from "./components/AppsContainer";
import DasboardCol from "./components/DashboardCol";
import Graphs from "./components/Graphs";
import NavbarRightContent from "./components/NabarRightContent";
import NavbarContent from "./components/NavbarContent";
import NavbarContentArray from "./NabarContentArray";


function App(){

    return <div >
        <div className="row">
            <div className ="col-3">
                <DasboardCol />
                <AppsContainer />
            </div>
            <div className ="col-9">
                <div className="Navbar">
                    {NavbarContentArray.map((item)=>{
                        return (
                            <NavbarContent 
                                key = {item.id}
                                leftImgUrl={item.imgUrl}
                            />
                        )
                    })}
                    <NavbarRightContent />
                </div>
                <Graphs />
            </div>
        </div>
    </div>
}

export default App;