import React from "react";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import DesktopMacIcon from '@material-ui/icons/DesktopMac';

function DasboardCol(){
    return <div className="dashboardCol">
                <img src="images/logo.png" alt="logo-img" className="logo-img" />
                <h1 className="logoText">Frest</h1> 
                <img src="images/logoCircle.png" alt="logoCircle-img" className="logoCircle-img" />
                <div className="dashboardContainer">
                    <div className = "inner-dashboardContainer" >
                        <p className="p-dashboard">
                          <DesktopMacIcon className="DesktopMacIcon"/>
                          <p className="p-dropDownHeader">Dashboard</p>
                          <span><ExpandMoreIcon/></span>
                         </p>
                    </div>
                    <div className="dropDown">
                        <a className="a-dashboard" href="#"> <ArrowForwardIcon className="ArrowForward" /> <p className="p-dropDown1">eCommerce</p></a>
                        <a className="a-dashboard" href="#"><ArrowForwardIcon className="ArrowForward" /> <p className="p-dropDown2">Analytics</p></a>
                    </div>
                </div>
            </div>
}

export default DasboardCol;