import React from "react";
import NavbarRightIcon from "./NavbarRightIcon";
import rightIconArr from "./NavbarRightIconArr";

function NavbarRightContent(){
    return (
        <div className="div-navbarRight">
            <a href="#">
                <img className="flag-icon" src="images/flag.png" alt=""/>
            </a>
            <p className="p-Eng">
                English
            </p>
            {rightIconArr.map( (icon)=>{
               return (
                    <NavbarRightIcon 
                        key={icon.id}
                        rightImgUrl = {icon.imgUrl}
                    />
               )
            })}
            <div className="profile" >
                <div className="profile-content">
                    <h1 >John Doe</h1>
                    <p >Available</p>
                </div>
                
                <img className="profileImg" src="images/profileImg.png"  alt="" />

            </div>
        </div>
    )
}

export default NavbarRightContent;