import React from "react";
import Col6Array from "../Col6Array";
import Col6 from "./Col6";

function Graphs(){
    return(
        <div className="row Graphs-row">
            <Col6 
                imgUrl={Col6Array[0].WebAnalytics}
            />
            <div className="col-6">
                
                <div className="row">
                    <Col6 
                        imgUrl ={Col6Array[1].Referral}
                    />
                    <Col6 
                        imgUrl ={Col6Array[2].Conversion}
                    />
                </div>

                <div className="row imperesion-row">
                    <Col6 
                        imgUrl ={Col6Array[3].imperesion}
                    />
                    
                        <div className="col-6">
                            <img className="GraphImgs" src="images/SmallConv.png" alt="" />
                            <img className="GraphImgs incomeImg" src="images/income.png" alt="" />
                        </div>
                       
                </div>
            </div>
        </div>
    )
}

export default Graphs;