import React from "react";
import AppsContent from "./appsContent";
import AppsContentArray from "../AppsContentArray";

function AppsContainer(){
    return (
        <div  className="main-appsContainer">
            <h1>APPS</h1>
            {AppsContentArray.map( (item)=>{
              return  <AppsContent
                         key={item.id}
                         imgUrl={item.imgUrl}
                         content={item.content}
                     />
            })}
            
        </div>
    )
}

export default AppsContainer;